.card {
    overflow: hidden;
    /* nasconde le parti che escono dell'immagine */
}

.card-img {
    width: 100%;
    height: auto;
    object-fit: cover;
    transition: transform 0.3s ease;
    display: block;
    aspect-ratio: 1/1;
}

.card:hover .card-img {
    transform: scale(1.05);
    /* zoom interno */
}

.card-item>a {
  text-decoration: none;
}

.cards-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  gap: 3rem; /* spaziatura opzionale */
}

.card {
  display: flex;
  flex-direction: column;
  height: 100%;
}

/* Base: 1 card per riga */
.card-item {
  flex: 1 1 100%;
  max-width: 100%;
}

/* Da 768px in su: 2 card per riga */
@media (min-width: 768px) {
  .card-item {
    flex: 1 1 calc(50% - 2rem);
    max-width: calc(50% - 2rem);
  }
}

/* Da 992px in su: 3 card per riga */
@media (min-width: 992px) {
  .card-item {
    flex: 1 1 calc(33.333% - 2rem);
    max-width: calc(33.333% - 2rem);
  }
}

a:hover {
    text-shadow: none;
}

/* Effetto hover solo sull'icona dentro la card */
.card:hover .card-footer small i {
  color: white;
  text-shadow: 0 0 10px #ff3333, 0 0 20px #ff3333, 0 0 30px #ff0000;
  transition: all 0.3s ease;
}

/* Stato normale dell’icona */
.card-footer small i {
  color: inherit;
  text-shadow: none;
  transition: all 0.3s ease;
}
